<template>
  <div class="container">
    <scroller :on-refresh="refresh" ref="myscroller">
      <li v-for="(item, index) in data" :key="index">
        <div
          @click="
            entryDetails(
              item.header,
              item.name,
              item.passtime,
              item.text,
              item.images,
              item.top_comments_name,
              item.top_comments_header,
              item.top_comments_content,
              item.up,
              type,
              imgurl,
              item.video,
              item.sid,
              item.uid
            )
          "
          class="box"
        >
          <div class="user-header-text">
            <img :src="item.header" alt class="head-portrait" />
            <span class="font-style">{{ item.name }}</span>
            <span class="font-style">{{ item.passtime }}</span>
          </div>
          <span>{{ item.text }}</span>
          <img :src="item.images" alt class="display-file" />
        </div>
        <span class="footer-font icon-pinglun"> {{ item.comment }}</span>
        <span
          class="footer-font"
          @click="giveUp(item)"
          :class="!item.sid ? upback : upago"
          v-if="item.sid"
          >{{ item.up }}</span
        >
        <transition name="move">
          <span
            class="footer-font"
            @click="giveUp(item)"
            :class="!item.sid ? upback : upago"
            v-if="!item.sid"
            >{{ item.up }}</span
          >
        </transition>
      </li>
      <button @click="removeArr">上一页</button>
      <button @click="addArr">下一页</button>
    </scroller>
  </div>
</template>
<script>
import { request } from "../../assets/network/request";
export default {
  props: ["pageH"],
  data() {
    return {
      data: "",
      upago: "icon-dianzan",
      upback: "icon-dianzan_1",
      defaultImg: 0,
      pageSize: 10,
      type: "热门",
      imgurl:
        "https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2269071459,2360885443&fm=26&gp=0.jpg",
    };
  },
  created() {
    request({
      url:
        "https://api.apiopen.top/getJoke?page=" +
        this.pageH +
        "&count=" +
        this.pageSize +
        "&type=image",
    })
      .then((res) => {
        this.data = res.data.result;
        console.log(this.data);
      })
      .catch((err) => {
        console.log(err, "获取失败！");
      });
  },
  methods: {
    // 下拉刷新
    refresh() {
      setTimeout(
        function () {
          this.goRefresh();
          this.$refs.myscroller.finishPullToRefresh(); //刷新完毕关闭刷新的转圈圈
        }.bind(this),
        1000
      );
    },
    goRefresh() {
      this.$emit("refreshH");
      setTimeout(() => {
        this.data = "";
        request({
          url:
            "https://api.apiopen.top/getJoke?page=" +
            this.pageH +
            "&count=" +
            this.pageSize +
            "&type=image",
        })
          .then((res) => {
            this.data = res.data.result;
            console.log(this.data);
          })
          .catch((err) => {
            alert(err);
          });
      }, 200);
      clearTimeout();
    },
    goPage() {
      this.data = "";
      console.log("123", this.page);
      request({
        url:
          "https://api.apiopen.top/getJoke?page=" +
          this.pageH +
          "&count=" +
          this.pageSize +
          "&type=image",
      })
        .then((res) => {
          this.data = res.data.result;
          console.log(this.data);
        })
        .catch((err) => {
          alert(err);
        });
    },
    addArr() {
      this.$emit("addArrH");
      setTimeout(() => {
        this.goPage();
      }, 200);
      clearTimeout();
    },
    removeArr() {
      this.$emit("removeArrH");
      setTimeout(() => {
       
          this.goPage();
        
      }, 200);
      clearTimeout();
    },
    giveUp(item) {
      if (item.sid) {
        //隐式转换string为number
        item.up -= 0;
        item.up += 1;
        item.sid = false;
      } else {
        item.sid = true;
        item.up -= 1;
      }
    },
    entryDetails(
      header,
      name,
      passtime,
      text,
      images,
      top_comments_name,
      top_comments_header,
      top_comments_content,
      up,
      type,
      imgurl,
      video,
      sid,
      uid
    ) {
      this.$router.push({
        name: "NewestDetails",
        query: {
          header: header,
          name: name,
          passtime: passtime,
          text: text,
          images: images,
          top_comments_name: top_comments_name,
          top_comments_header: top_comments_header,
          top_comments_content: top_comments_content,
          up: up,
          type: type,
          imgurl: imgurl,
          video: video,
          sid: sid,
          uid: uid,
        },
      });
    },
  },
};
</script>
<style lang="scss" scoped>
div {
  margin-top: 80px;

  .move-enter {
    transform: translateX(-100px);
  }
  .move-enter-active {
    transition: all 0.5s linear;
  }

  li {
    padding: 10px;
    border-bottom: 10px;
    list-style: none;

    .user-header-text {
      margin-top: 0;
      position: relative;
      margin-bottom: 5px;
      height: 30px;
    }

    .head-portrait {
      width: 30px;
      height: 30px;
      border-radius: 50%;
    }

    .display-file {
      width:100%;
      height: 100%;
      margin: 5px 0;
      background-image: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1602496626402&di=7e821dac9cdb07fc6d8b973afd1978df&imgtype=0&src=http%3A%2F%2Fc-ssl.duitang.com%2Fuploads%2Fitem%2F201808%2F01%2F20180801230124_hyFUW.thumb.1000_0.gif");
      background-size: cover;
    }

    .font-style {
      display: inline-block;
      margin: 0 10px;
      position: relative;
      top: -7.5px;
      line-height: 30px;
      height: 30px;
      font-size: 0.12rem;
      color: #aaa;
    }

    .font-style:last-child {
      position: absolute;
      top: 3px;
      right: 0;
    }

    .box {
      margin: 0;
    }

    .footer-font {
      margin: 0 10px;
      color: #aaa;
      font-size: 14px;
    }
    .footer-font:last-child {
      float: right;
    }
  }

  button {
    margin-bottom: 30px;
    margin-left: 10px;
    width: 45%;
    height: 50px;
    border-style: none;
    background-color: #fff;
    outline: 0;
  }
}
</style>